import React, { createContext, useState, useContext, useEffect } from "react";
import { styled } from "@mui/material/styles";
import FormGroup from "@mui/material/FormGroup";
import FormControlLabel from "@mui/material/FormControlLabel";
import Switch from "@mui/material/Switch";
import Box from "@mui/material/Box";
import "./App.css";
import Button from "@mui/material/Button";
import LockOpenOutlinedIcon from "@mui/icons-material/LockOpenOutlined";
import SettingsIcon from "@mui/icons-material/Settings";
import LockOutlinedIcon from "@mui/icons-material/LockOutlined";
import AgricultureIcon from "@mui/icons-material/Agriculture";
import SaveAltIcon from "@mui/icons-material/SaveAlt";
import Modal from "@mui/material/Modal";
import Typography from "@mui/material/Typography";
import axios from "axios";
import ThermostatIcon from "@mui/icons-material/Thermostat";
import OpacitySharpIcon from "@mui/icons-material/OpacitySharp";
import { io } from "socket.io-client";
import Checkbox from "@mui/material/Checkbox";

const styleL = {
  position: "absolute",
  top: "50%",
  left: "50%",
  transform: "translate(-50%, -50%)",
  width: 400,
  bgcolor: "background.paper",
  boxShadow: 24,
  p: 4
};
const styleD = {
  position: "absolute",
  top: "50%",
  left: "50%",
  transform: "translate(-50%, -50%)",
  width: 400,
  color: "#fff",
  bgcolor: "#100b30",
  boxShadow: 24,
  p: 4
};

const ThemeContext = createContext();

const IOSSwitch = styled((props) => (
  <Switch focusVisibleClassName=".Mui-focusVisible" disableRipple {...props} />
))(({ theme }) => ({
  width: 42,
  height: 26,
  padding: 0,
  "& .MuiSwitch-switchBase": {
    padding: 0,
    margin: 2,
    transitionDuration: "300ms",
    "&.Mui-checked": {
      transform: "translateX(16px)",
      color: "#fff",
      "& + .MuiSwitch-track": {
        backgroundColor: theme.palette.mode === "dark" ? "#2ECA45" : "#65C466",
        opacity: 1,
        border: 0
      },
      "&.Mui-disabled + .MuiSwitch-track": {
        opacity: 0.5
      }
    },
    "&.Mui-focusVisible .MuiSwitch-thumb": {
      color: "#33cf4d",
      border: "6px solid #fff"
    },
    "&.Mui-disabled .MuiSwitch-thumb": {
      color:
        theme.palette.mode === "light"
          ? theme.palette.grey[100]
          : theme.palette.grey[600]
    },
    "&.Mui-disabled + .MuiSwitch-track": {
      opacity: theme.palette.mode === "light" ? 0.7 : 0.3
    }
  },
  "& .MuiSwitch-thumb": {
    boxSizing: "border-box",
    width: 22,
    height: 22
  },
  "& .MuiSwitch-track": {
    borderRadius: 26 / 2,
    backgroundColor: theme.palette.mode === "light" ? "#E9E9EA" : "#39393D",
    opacity: 1,
    transition: theme.transitions.create(["background-color"], {
      duration: 500
    })
  }
}));

const api = "https://mui.anuchadev.com";

export default function App() {
  const [theme, setTheme] = useState("dark");
  const [unlock, setUnlock] = useState(false);

  const toggleTheme = () => {
    setTheme((curr) => (curr === "light" ? "dark" : "light"));
  };

  const handleChange = (data, stateSw) => {
    var state = stateSw;
    if (stateSw === true) {
      state = 1;
    } else {
      state = 0;
    }
    axios.get(api + "/updatestatesw/" + data + "/" + state).then((res) => {
      console.log(res);
    });
  };

  const [open, setOpen] = useState(false);
  // openPass
  const [swID, setSwID] = useState();
  const [swName, setSwName] = useState();
  const [swTimeOn, setSwTimeOn] = useState();
  const [swTimeOff, setSwTimeOff] = useState();
  const [swTimeOn2, setSwTimeOn2] = useState();
  const [swTimeOff2, setSwTimeOff2] = useState();
  const [check1, setCheck1] = useState();
  const [check2, setCheck2] = useState();

  const handleOpen = (
    data,
    name,
    timeOn,
    timeOff,
    timeOn2,
    timeOff2,
    st1,
    st2
  ) => {
    setSwID(data);
    setSwName(name);
    setSwTimeOn(timeOn);
    setSwTimeOff(timeOff);
    setSwTimeOn2(timeOn2);
    setSwTimeOff2(timeOff2);
    setCheck1(st1);
    setCheck2(st2);
    setOpen(true);
  };

  const [time, setTime] = useState("fetching");

  const [dateTHT, setDateTHT] = useState(" ");
  const [dateTHD, setDateTHD] = useState(" ");
  const [dataSW, setDataSW] = useState([]);

  useEffect(() => {
    getdata();
    getDataTem();

    const socket = io("https://mui.anuchadev.com");

    socket.on("connect", () => console.log(socket.id));
    socket.on("connect_error", () => {
      setTimeout(() => socket.connect(), 5000);
    });
    socket.on("sw-state", (data) => {
      console.log(data.data);
      setDataSW(data.data);
    });
    socket.on("data-rial", (data) => {
      var todayTime = new Date().toLocaleTimeString("en-GB", {
        timeZone: "Asia/Kolkata"
      });
      var todayDate = new Date().toLocaleDateString("en-GB");
      setDateTHT(todayTime);
      setDateTHD(todayDate);
      setDataTem(data.tem);
      setDataHum(data.hum);
    });
    socket.on("disconnect", () => {
      setTime("server disconnected");
      console.log(time);
    });
  }, []);

  const getdata = () => {
    axios.get(api + "/getswdata").then((res) => {
      const { data } = res;
      setDataSW(data.data);
      console.log("device_id", data.data);
    });
  };

  const [dataTem, setDataTem] = useState([]);
  const [dataHum, setDataHum] = useState([]);

  const getDataTem = () => {
    axios.get(api + "/getrialdata").then((res) => {
      const { data } = res;
      var todayTime = new Date().toLocaleTimeString("en-GB", {
        timeZone: "Asia/Kolkata"
      });
      var todayDate = new Date().toLocaleDateString("en-GB");
      dateTHT === " " ? setDateTHT(todayTime) : undefined;
      dateTHD === " " ? setDateTHD(todayDate) : undefined;
      setDataTem(data.data[0].raildata_tem);
      setDataHum(data.data[0].raildata_hum);
      var dt = data.data[0].raildata_date;
      console.log(dt.toString("YYYY-MM-dd"));
    });
  };

  console.log("dataTem", dataTem);
  return (
    <ThemeContext.Provider value={{ theme, toggleTheme }}>
      <div className="App" id={theme}>
        <div className="nav">
          <div className="container-xl">
            <div className="row">
              <div className="col-sm-5">
                {" "}
                <div className="d-flex tt">
                  <div style={{ marginTop: -10, marginRight: 10 }}>
                    <AgricultureIcon sx={{ fontSize: 40 }} />
                  </div>
                  <div>
                    <h2>Smart Farm System</h2>{" "}
                  </div>
                </div>
              </div>
              <div className="col-sm"></div>
              <div className="col-sm-3">
                <div className="tt" style={{ justifyContent: "end" }}>
                  {theme === "light" ? "Light Mode" : "Dark Mode"}
                  &emsp;
                  <FormGroup>
                    <FormControlLabel
                      control={<IOSSwitch sx={{ m: 1 }} />}
                      onChange={toggleTheme}
                    />
                  </FormGroup>
                  {unlock ? (
                    <LockOpenOutlinedIcon
                      style={{ cursor: "pointer" }}
                      onClick={() => setUnlock(false)}
                    />
                  ) : (
                    <LockOutlinedIcon
                      style={{ cursor: "pointer" }}
                      onClick={() => handleOpen("lock")}
                    />
                  )}
                </div>
              </div>
            </div>
          </div>
        </div>
        <div className="container-xl">
          <div className="row">
            <div className="col-sm-5 p-2 ">
              <div className="head main">
                <div style={{ display: "block", padding: 20 }}>
                  <img
                    style={{ width: "100%", marginBottom: 15 }}
                    src="https://www.weruwan.ac.th/img/logo.png"
                  />
                  <hr />
                  <img
                    style={{ width: "100%" }}
                    src="https://devcm.info/img/logo.png"
                  />
                </div>
              </div>
            </div>

            <div className="col-sm p-2 ">
              <div className="head main">
                <div
                  style={{ display: "block", padding: 20, textAlign: "center" }}
                >
                  <h1>
                    <ThermostatIcon sx={{ fontSize: 45 }} />
                    temperature
                  </h1>
                  <br />
                  <h1>{dataTem} °C</h1>
                  <br />
                  <h4>
                    {dateTHT} : {dateTHD}
                  </h4>
                </div>
              </div>
            </div>

            <div className="col-sm p-2 ">
              <div className="head main">
                <div
                  style={{ display: "block", padding: 20, textAlign: "center" }}
                >
                  <h1>
                    <OpacitySharpIcon sx={{ fontSize: 40 }} />
                    humidity
                  </h1>
                  <br />
                  <h1>{dataHum} %</h1>

                  <br />
                  <h4>
                    {dateTHT} : {dateTHD}
                  </h4>
                </div>
              </div>
            </div>
          </div>
          <div className="row">
            {dataSW.map((index, key) => {
              return (
                <div className="col-sm-3 p-2">
                  <div className="main p-3">
                    <div className="sw">
                      <div style={{ flexGrow: 1 }}>
                        {theme === "light" ? "ON" : "OFF"}
                      </div>

                      <FormGroup>
                        <FormControlLabel
                          control={
                            <IOSSwitch
                              sx={{ m: 1 }}
                              checked={index.device_state === 1 ? true : false}
                            />
                          }
                          onChange={(event) => {
                            handleChange(index.device_id, !index.device_state);
                          }}
                        />
                      </FormGroup>
                    </div>
                    <hr />
                    <div className="sw ">
                      <p style={{ flexGrow: 1 }}>Device Name :</p>
                      <p style={{ marginRight: "1.5rem" }}>
                        {index.device_name}
                      </p>
                    </div>
                    <b>
                      <p>Round 1</p>
                    </b>
                    <div className="sw ">
                      <p style={{ flexGrow: 1 }}>Time Start :</p>
                      <p style={{ marginRight: "1.5rem" }}>
                        {index.device_stateT1 === 1
                          ? index.device_time_on1
                          : "off"}
                      </p>
                    </div>

                    <div className="sw ">
                      <p style={{ flexGrow: 1 }}>Time End :</p>
                      <p style={{ marginRight: "1.5rem" }}>
                        {index.device_stateT1 === 1
                          ? index.device_time_off1
                          : "off"}
                      </p>
                    </div>
                    <b>
                      <p>Round 2</p>
                    </b>
                    <div className="sw ">
                      <p style={{ flexGrow: 1 }}>Time Start :</p>
                      <p style={{ marginRight: "1.5rem" }}>
                        {index.device_stateT2 === 1
                          ? index.device_time_on2
                          : "off"}
                      </p>
                    </div>

                    <div className="sw ">
                      <p style={{ flexGrow: 1 }}>Time End :</p>
                      <p style={{ marginRight: "1.5rem" }}>
                        {index.device_stateT2 === 1
                          ? index.device_time_off2
                          : "off"}
                      </p>
                    </div>

                    <div className="sw ">
                      <div style={{ flexGrow: 1 }}></div>
                      <div style={{ marginRight: "1.5rem" }}>
                        <Button
                          variant="outlined"
                          startIcon={<SettingsIcon />}
                          onClick={() =>
                            handleOpen(
                              index.device_id,
                              index.device_name,
                              index.device_time_on1,
                              index.device_time_off1,
                              index.device_time_on2,
                              index.device_time_off2,
                              index.device_stateT1,
                              index.device_stateT2
                            )
                          }
                        >
                          Setting
                        </Button>
                      </div>
                    </div>
                  </div>
                </div>
              );
            })}
          </div>
          <div style={{ marginTop: 29, textAlign: "end", widows: "100%" }}>
            <p>
              develop by <a href="https://devcm.info/">CM-DEVELOPER</a>
            </p>
          </div>
        </div>
        {open ? (
          <ModalSetting
            setOpen={setOpen}
            open={open}
            swID={swID}
            theme={theme}
            setUnlock={setUnlock}
            swName={swName}
            swTimeOn={swTimeOn}
            swTimeOff={swTimeOff}
            swTimeOn2={swTimeOn2}
            swTimeOff2={swTimeOff2}
            check1={check1}
            check2={check2}
          />
        ) : undefined}
      </div>
    </ThemeContext.Provider>
  );
}

function ModalSetting({
  setOpen,
  open,
  swID,
  theme,
  setUnlock,
  swName,
  swTimeOn,
  swTimeOff,
  swTimeOn2,
  swTimeOff2,
  check1,
  check2
}) {
  const handleClose = () => setOpen(false);
  const [passWord, setPassWord] = useState("");
  const [name, setName] = useState(swName);
  const [timeS, setTimeS] = useState(swTimeOn);
  const [timeE, setTimeE] = useState(swTimeOff);
  const [timeS2, setTimeS2] = useState(swTimeOn2);
  const [timeE2, setTimeE2] = useState(swTimeOff2);
  const [checked1, setChecked1] = useState(check1);
  const [checked2, setChecked2] = useState(check2);

  const handleChange1 = () => {
    setChecked1(!checked1);
  };
  const handleChange2 = (event) => {
    setChecked2(!checked2);
  };
  const handleChangePassWord = (event) => {
    setPassWord(event.target.value);
  };
  const handleChangeName = (event) => {
    setName(event.target.value);
  };
  const handleChangeTimeS = (event) => {
    setTimeS(event.target.value);
  };
  const handleChangeTimeE = (event) => {
    setTimeE(event.target.value);
  };
  const handleChangeTimeS2 = (event) => {
    setTimeS2(event.target.value);
  };
  const handleChangeTimeE2 = (event) => {
    setTimeE2(event.target.value);
  };

  const checkPass = () => {
    if (passWord !== "abc1234") {
      return (
        <script>
          function myFunction() {alert("Invalid password please try again")}
        </script>
      );
    } else {
      setUnlock(true);
      handleClose();
    }
  };

  const updateDataSw = async (name, s, e, s2, e2, st1, st2) => {
    // GET https://mui.anuchadev.com/updatdatasw/5/sw5/67/77/7/9 HTTP/1.1
    var state_time1 = st1 ? 1 : 0;
    var state_time2 = st2 ? 1 : 0;
    console.log(
      api +
        "/updatdatasw/" +
        swID +
        "/" +
        name +
        "/" +
        s +
        "/" +
        e +
        "/" +
        s2 +
        "/" +
        e2 +
        "/" +
        state_time1 +
        "/" +
        state_time2
    );
    await axios
      .get(
        api +
          "/updatdatasw/" +
          swID +
          "/" +
          name +
          "/" +
          s +
          "/" +
          e +
          "/" +
          s2 +
          "/" +
          e2 +
          "/" +
          state_time1 +
          "/" +
          state_time2
      )
      .then((res) => {
        console.log(res.status);
        if (res.status === 200) {
          setTimeout(() => {
            handleClose();
          }, 200);
        } else {
        }
      });
  };

  console.log(passWord);
  console.log("madol", open);
  console.log("swID", swID);
  return (
    <Modal
      keepMounted
      open={open}
      onClose={handleClose}
      aria-labelledby="keep-mounted-modal-title"
      aria-describedby="keep-mounted-modal-description"
    >
      {swID === "lock" ? (
        <Box sx={theme === "light" ? styleL : styleD} className="text-center">
          <Typography id="keep-mounted-modal-title" variant="h6" component="h2">
            Enter password for unlock system
          </Typography>
          <Typography id="keep-mounted-modal-description" sx={{ mt: 2 }}>
            <input
              type="password"
              className="form-control"
              onChange={handleChangePassWord}
            />
            <button
              type="button"
              className="btn btn-outline-primary btn-lg btn-block"
              style={{ width: "80%", marginTop: 20 }}
              onClick={checkPass}
            >
              Confirm
            </button>
          </Typography>
        </Box>
      ) : (
        <Box sx={theme === "light" ? styleL : styleD}>
          <Typography id="keep-mounted-modal-title" variant="h6" component="h2">
            Change device
          </Typography>
          <Typography id="keep-mounted-modal-description" sx={{ mt: 2 }}>
            <div className="sw " style={{ marginTop: "0.5rem" }}>
              <p style={{ flexGrow: 1, marginTop: "1rem" }}>Device Name :</p>
              <input
                type="text"
                className="form-control"
                style={{ width: "13rem" }}
                value={name}
                onChange={handleChangeName}
              />
            </div>
            <b className="text-start d-flex">
              <p
                style={{ paddingTop: "1rem", paddingLeft: "1rem", flexGrow: 1 }}
              >
                Round 1
              </p>
              <Checkbox
                checked={checked1}
                onChange={handleChange1}
                inputProps={{ "aria-label": "controlled" }}
              />
              <p style={{ paddingTop: "1rem" }}>{checked1 ? "on" : "off"}</p>
            </b>
            <div className="sw " style={{ marginTop: "0.5rem" }}>
              <p style={{ flexGrow: 1, marginTop: "1rem" }}>Time Start :</p>
              <input
                type="time"
                className="form-control"
                style={{ width: "13rem" }}
                value={timeS}
                onChange={handleChangeTimeS}
                disabled={!checked1}
              />
            </div>

            <div className="sw " style={{ marginTop: "0.5rem" }}>
              <p style={{ flexGrow: 1, marginTop: "1rem" }}>Time End :</p>
              <input
                type="time"
                className="form-control"
                style={{ width: "13rem" }}
                value={timeE}
                onChange={handleChangeTimeE}
                disabled={!checked1}
              />
            </div>
            <b className="text-start d-flex">
              <p
                style={{ paddingTop: "1rem", paddingLeft: "1rem", flexGrow: 1 }}
              >
                Round 2
              </p>
              <Checkbox
                checked={checked2}
                onChange={handleChange2}
                inputProps={{ "aria-label": "controlled" }}
              />
              <p style={{ paddingTop: "1rem" }}>{checked2 ? "on" : "off"}</p>
            </b>
            <div className="sw " style={{ marginTop: "0.5rem" }}>
              <p style={{ flexGrow: 1, marginTop: "1rem" }}>Time Start :</p>
              <input
                type="time"
                className="form-control"
                style={{ width: "13rem" }}
                value={timeS2}
                onChange={handleChangeTimeS2}
                disabled={!checked2}
              />
            </div>

            <div className="sw " style={{ marginTop: "0.5rem" }}>
              <p style={{ flexGrow: 1, marginTop: "1rem" }}>Time End :</p>
              <input
                type="time"
                className="form-control"
                style={{ width: "13rem" }}
                value={timeE2}
                onChange={handleChangeTimeE2}
                disabled={!checked2}
              />
            </div>

            <div className="sw " style={{ marginTop: "1rem" }}>
              <div style={{ flexGrow: 1 }}></div>
              <div>
                <Button
                  variant="outlined"
                  startIcon={<SaveAltIcon />}
                  onClick={() =>
                    updateDataSw(
                      name,
                      timeS,
                      timeE,
                      timeS2,
                      timeE2,
                      checked1,
                      checked2
                    )
                  }
                >
                  Save
                </Button>
              </div>
            </div>
          </Typography>
        </Box>
      )}
    </Modal>
  );
}
